.faq {
	background-color: var(--light-white-color-400);
	// .faq__container

	&__container {
	}

	// .faq__content

	&__content {
		&:not(:last-child) {
			margin-bottom: toRem(65);
			@media (max-width: $mobile) {
				margin-bottom: toRem(37);
			}
		}
		&.content {
			.content__title {
			}
		}
	}

	// .faq__spollers

	&__spollers {
	}
}

.spollers {
	display: grid;
	gap: toRem(46);
	// transition: padding 0.3s;
	// padding: 0 toRem(100) 0 0;

	@media (min-width: $mobile) {
		max-width: 65.4375rem;
		margin: 0 auto;
	}

	@include adaptiveValue("padding-left", 100, 50, 0, $containerWidth, 767.98);
	@include adaptiveValue("padding-right", 100, 50, 0, $containerWidth, 767.98);
	@media (max-width: $mobile) {
		gap: toRem(34);
	}
	@media (max-width: toEm(600)) {
		padding-left: 0;
		padding-right: toRem(40);
	}
}
// Спойлер
.spollers__item {
	font-size: toRem(20);
}
// Заголовок спойлера
.spollers__title {
	display: flex;
	justify-content: space-between;
	align-items: center;
	gap: toRem(10);
	width: 100%;
	cursor: pointer;
	text-align: left;
	padding: toRem(21.5) toRem(54);
	border-radius: toRem(12);
	position: relative;
	list-style: none;
	font-size: toRem(25);
	font-weight: 500;
	line-height: 1.3;
	background-color: var(--white-color);
	transition: background-color 0.3s;
	@media (any-hover: hover) {
		&:hover {
			background-color: var(--light-white-color-200);
		}
	}
	&::marker,
	&::-webkit-details-marker {
		display: none;
	}
	@media (max-width: $mobile) {
		padding: toRem(10) toRem(20) toRem(10) toRem(22);
		gap: toRem(5);
	}
	@media (max-width: toEm(600)) {
		font-size: toRem(16);
	}
	i {
		flex: 0 0 toRem(30);
		width: toRem(30);
		height: toRem(30);
		border-radius: 50%;
		display: flex;
		justify-content: center;
		align-items: center;
		border: toRem(2) solid var(--blue-color);
		img {
			width: toRem(15);
			height: toRem(15);
			transition: transform 0.3s;
		}
	}
	&._spoller-active {
		background-color: var(--white-color);
		i {
			img {
				transform: scale(-1);
			}
		}
	}
}
// Контент спойлера
.spollers__body {
	padding: toRem(10) toRem(60) toRem(10) toRem(40);
	border-radius: toRem(16);
	background-color: var(--blue-color);
	position: relative;

	@include adaptiveValue("right", -100, -34, 0, $containerWidth, 767.98);
	@media (max-width: $mobile) {
		padding: toRem(20);
	}
	p {
		color: var(--white-color);
		font-size: toRem(25);
		max-width: toRem(730);
		line-height: 1.3;
		@media (max-width: toEm(600)) {
			font-size: toRem(14);
		}
	}
}
