.form {
	display: grid;
	gap: toRem(25);
	@media (max-width: $mobile) {
		display: none;
	}
	// .form__row

	&__row {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: toRem(30);
		align-items: end;
		@media (max-width: $tablet) {
			gap: toRem(15);
		}
		&_wide {
			grid-template-columns: 1fr;
		}
	}

	// .form__line

	&__line {
	}

	&__label {
		display: block;
		font-size: toRem(18);
		font-weight: 600;
		margin-bottom: toRem(10);
	}

	// .form__input

	&__input {
	}

	// .form__send

	&__send {
		margin-right: auto;
		display: flex;
		align-items: center;
		gap: toRem(6);
		i {
			width: toRem(24);
			height: toRem(24);
			display: flex;
			justify-content: center;
			align-items: center;
			img {
				max-width: 100%;
			}
		}
		&.button {
			font-size: toRem(14);
			font-weight: 500;
			line-height: 1.6;
			padding: toRem(13) toRem(20);
			@media (max-width: $mobile) {
			}
		}
	}
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}
.input {
	border-radius: toRem(8) !important;
	width: 100%;
	display: block;
	font-size: toRem(16);
	padding: 0 toRem(16);
	height: toRem(58);
	background-color: var(--white-color);
	transition: all 0.3s;
	color: var(--primary-color);
	outline: toRem(1) solid var(--white-color);
	&::placeholder {
		color: #94a3b8;
	}
	&:focus {
		outline-color: var(--primary-color);
	}
}
.input[placeholder]:not([data-placeholder-nohiden]) {
	&::placeholder {
		transition: opacity 0.3s;
	}
	&:focus::placeholder {
		opacity: 0;
	}
}

textarea.input {
	resize: vertical;
	padding: toRem(16);
	min-height: toRem(128);
}
