body {
	// The scroll is blocked

	// The site is downloaded
	.loaded & {
	}
}
.wrapper {
	min-height: 100%;
	display: flex;
	flex-direction: column;
	overflow: hidden;
	@supports (overflow: clip) {
		overflow: clip;
	}
	// We press Footer
	> main {
		flex: 1 1 auto;
	}
	// Fix for sliders
	> * {
		min-width: 0;
	}
}

//========================================================================================================================================================
.page {
	--padding-top-section: 70px;
	--padding-bottom-section: 70px;
	@media (max-width: $mobile) {
		--padding-top-section: 40px;
		--padding-bottom-section: 40px;
	}
	// .page__hero

	&__hero {
	}
	// .page__about-us

	&__about-us {
		padding-top: toRem(90);
		padding-bottom: toRem(90);
		@media (max-width: $mobile) {
			padding-top: toRem(30);
			padding-bottom: toRem(30);
		}
	}

	&__programs {
		padding-top: var(--padding-top-section);
		padding-bottom: var(--padding-bottom-section);
	}
	&__journey {
		padding-top: var(--padding-top-section);
		padding-bottom: var(--padding-bottom-section);
	}
	// .page__care

	&__care {
		padding-top: var(--padding-top-section);
		padding-bottom: var(--padding-bottom-section);
	}

	&__testimonials {
		padding-top: var(--padding-top-section);
		padding-bottom: var(--padding-bottom-section);
	}
	// .page__faq

	&__faq {
		padding-top: var(--padding-top-section);
		padding-bottom: var(--padding-bottom-section);
	}
}

.content {
	// .content__title

	&__title {
		font-size: toRem(40);
		@include adaptiveValue("font-size", 40, 28);
		font-weight: 700;
		line-height: 1.25;
		letter-spacing: toRem(0.4);
		&:not(:last-child) {
			margin-bottom: toRem(20);
		}
		@media (max-width: $mobileSmall) {
			&:not(:last-child) {
				margin-bottom: toRem(10);
			}
		}
		&_big {
			@include adaptiveValue("font-size", 50, 22);
		}
	}

	// .content__text

	&__text {
		line-height: 1.3;
		&:not(:last-child) {
			margin-bottom: toRem(10);
		}
		@media (max-width: $mobileSmall) {
			font-size: toRem(16);
		}
	}

	// .content__button

	&__button {
	}
	&_center {
		text-align: center;
	}
	&_left {
		text-align: left;
	}
}

.text {
	p {
		&:not(:last-child) {
			margin-bottom: toRem(10);
		}
	}
}
.rating {
	display: flex;
	align-items: center;
	gap: toRem(5);
	position: relative;
	bottom: toRem(3);
	// .rating__item

	&__item {
		flex: 0 0 toRem(25);
		width: toRem(25);
		height: toRem(25);
		img {
			max-width: 100%;
		}
	}
}

.ibg {
	position: relative;
	img {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		object-fit: cover;
	}
	&_contain {
		img {
			object-fit: contain;
		}
	}
}

.sr-only {
	position: absolute;
	width: 1px;
	height: 1px;
	padding: 0;
	margin: -1px;
	overflow: hidden;
	clip: rect(0, 0, 0, 0);
	white-space: nowrap;
	border-width: 0;
}
