@use "sass:math" as math;
// RESET ================

// Global Settings! =====
// The width of the canvas (layout)
$maxWidth: 1440;
// The width of the restrictive container
$maxWidthContainer: 1232; // (0 = no restriction)
// General indentation from a container
$containerPadding: 40; // (30 = 15px left and right, 0 = no retreat)
// Mixins
@import "../base/mixins";
// Fonts

$fontSize: toRem(25);
$minWidth: 320;
// Breikpoints
$tablet: toEm(991.98); // tablets, some phones in horizontal position
$mobile: toEm(767.98); // phones l
$mobileSmall: toEm(479.98); // phones with
// Reset styles
@import "../base/null";

// ====================================================================
// ====================================================================
// ====================================================================

$containerWidth: $maxWidthContainer + $containerPadding;
[class*="__container"] {
	@if ($maxWidthContainer>0) {
		max-width: toRem($containerWidth);
		margin: 0 auto;
	}
	@if ($containerPadding>0) {
		@if ($maxWidthContainer>0) {
			padding: 0 toRem(math.div($containerPadding, 2));
		} @else {
			@include adaptiveValue("padding-left", math.div($containerPadding, 2), 15);
			@include adaptiveValue("padding-right", math.div($containerPadding, 2), 15);
		}
	}
}
