.programs {
	// .programs__container

	&__container {
	}

	// .programs__content

	&__content {
		&:not(:last-child) {
			margin-bottom: toRem(80);
			@media (max-width: $mobile) {
				margin-bottom: toRem(60);
			}
		}
		&.content {
			.content__title {
			}
		}
	}

	// .programs__list

	&__list {
		display: grid;
		gap: toRem(63);
		&:not(:last-child) {
			margin-bottom: toRem(40);
			@media (max-width: $mobile) {
				margin-bottom: toRem(30);
			}
		}
		@media (min-width: toEm(601)) {
			grid-template-columns: repeat(2, 1fr);
		}
		@media (max-width: toEm(600)) {
			// gap: toRem(30);
		}
	}

	// .programs__item

	&__item {
	}

	// .programs__text

	&__text {
		text-align: center;
		line-height: 1.3;
		font-size: toRem(24);
		@media (max-width: $mobileSmall) {
			font-size: toRem(16);
		}
	}
}

.item-program {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	// .item-program__picture

	&__picture {
		width: 100%;
		padding-bottom: percent(418, 584);
		&::before {
			content: "";
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 87.598%;
			height: 122.388%;
			border-radius: 50%;
			z-index: -1;
		}
		&:not(:last-child) {
			margin-bottom: toRem(30);
			@media (max-width: $mobileSmall) {
				margin-bottom: toRem(10);
			}
		}
		&_dark-bg {
			&::before {
				background-color: rgba(48, 122, 196, 0.2);
			}
		}
		&_light-bg {
			&::before {
				background-color: rgba(227, 234, 255, 0.2);
				@media (max-width: $mobileSmall) {
					background-color: rgba(48, 122, 196, 0.2);
				}
			}
		}
	}

	// .item-program__link

	&__link {
	}
}
