.header {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 99;
	background-color: var(--white-color);
	padding-top: toRem(5);
	padding-bottom: toRem(5);

	// .header__container

	&__container {
		display: flex;
		align-items: center;
		justify-content: space-between;
		column-gap: toRem(10);
		min-height: toRem(100);
		@media (max-width: $mobile) {
			min-height: toRem(75);
		}
	}

	// .header__text

	&__text {
		font-size: toRem(24);
		line-height: 1.25;
		font-weight: 700;
		font-family: var(--brand-font);
		@media (max-width: $mobileSmall) {
			font-size: toRem(20);
		}
	}

	// .header__button

	form {
		flex-shrink: 0;
		.button {
			&_blue {
				padding-left: toRem(80);
				padding-right: toRem(80);
				@media (max-width: $mobileSmall) {
					padding-left: toRem(30);
					padding-right: toRem(30);
				}
			}
		}
	}
}
