.care {
	background-color: var(--light-white-color-400);
	// .care__container

	&__container {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		gap: toRem(20);
	}

	// .care__content

	&__content {
		&.content {
			// .content_center

			.content__title {
			}

			.content__text {
			}
		}
	}

	// .care__button

	&__button {
	}
	table {
		border-collapse: collapse;
		font-size: toRem(18);
		max-width: toRem(1017);
		width: 100%;

		@media (max-width: $mobile) {
			font-size: toRem(12);
		}
		@media (max-width: toEm(359)) {
			font-size: toRem(10);
		}
	}

	thead {
		font-size: toRem(15);
		@media (max-width: $mobile) {
			font-size: toRem(12);
		}
		@media (max-width: toEm(359)) {
			font-size: toRem(10);
		}
		color: var(--white-color);
		tr {
			height: toRem(84);
			@media (max-width: $mobile) {
				height: toRem(42);
			}

			th {
				text-align: left;
				font-weight: 400;
				&:nth-child(1),
				&:nth-child(2) {
					background-color: var(--blue-color);
					border: none;
				}
				&:nth-child(3),
				&:nth-child(4) {
					border: none;
					background-color: var(--orange-color);
				}
			}
		}
	}

	tbody {
		margin-top: toRem(10);
		vertical-align: top;
		@media (max-width: $mobile) {
			vertical-align: middle;
		}
		tr {
			border-bottom: toRem(2) solid #606060;

			&:last-child {
				border-bottom: none;
			}
		}
	}

	th,
	td {
		padding: toRem(40) toRem(30);
		text-align: left;
		line-height: 1.3;
		@media (max-width: $mobile) {
			padding: toRem(10);
		}
		@media (max-width: toEm(359)) {
			padding: toRem(5);
		}
	}

	th {
		background-color: #f2f2f2;
	}

	th,
	td:nth-child(1) {
		color: var(--white-color);
		background-color: var(--dark-blue-color);
		text-align: center;
		@media (min-width: $mobile) {
			width: 60px;
		}
	}

	td:nth-child(2) {
		background-color: #ffffff;
		@media (min-width: $mobile) {
			width: 218px;
		}
	}

	td:nth-child(3) {
		background-color: var(--light-white-color-100);
		@media (min-width: $mobile) {
			width: 169px;
		}
	}
	td:nth-child(4) {
		@media (min-width: $mobile) {
			width: 169px;
		}
		background-color: var(--light-white-color-100);
		border-left: toRem(2) solid var(--white-color);
	}
}
