.journey {
	background-color: var(--light-white-color-400);
	// .journey__container

	&__container {
	}

	// .journey__content

	&__content {
		max-width: toRem(837);
		margin: 0 auto;
		&:not(:last-child) {
			margin-bottom: toRem(46);
			@media (max-width: $mobileSmall) {
				margin-bottom: toRem(30);
			}
		}
		&.content {
			// .content_center

			&_center {
			}

			.content__title {
				@media (max-width: $mobileSmall) {
					&:not(:last-child) {
						margin-bottom: toRem(15);
					}
				}
			}

			// .content__text

			&__text {
			}
		}
	}

	// .journey__list

	&__list {
		display: grid;
		gap: toRem(30);
		@media (min-width: $mobile) {
			grid-template-columns: repeat(3, 1fr);
		}
		@media (max-width: $mobile) {
			gap: toRem(20);
		}
	}

	// .journey__item

	&__item {
	}
}

.item-journey {
	text-align: center;
	background-color: var(--white-color);
	border-radius: 0 0 toRem(34) toRem(34);
	overflow: hidden;

	border-radius: toRem(34);
	@media (max-width: $mobileSmall) {
		display: flex;
		flex-direction: column;
	}
	// .item-journey__image

	&__image {
		display: block;
		align-self: center;
		width: 100%;
		border-radius: toRem(34);
		overflow: hidden;
		padding-bottom: percent(272, 390);
	}

	// .item-journey__title

	&__title {
		font-weight: 500;
		line-height: 1.3;
		padding: toRem(35) toRem(20) toRem(50) toRem(20);
		max-width: toRem(300);
		margin: 0 auto;
		line-height: 1.5;

		@media (max-width: $tablet) {
			font-size: toRem(20);
			padding: toRem(20);
		}
		@media (max-width: $mobile) {
			font-size: toRem(25);
			max-width: toRem(500);
		}
		@media (max-width: $mobileSmall) {
			font-size: toRem(20);
		}
		// @media (max-width: $mobileSmall) {
		// 	height: 100%;
		// 	display: flex;
		// 	align-items: center;
		// }
	}
	@media (max-width: $mobileSmall) {
		// min-height: toRem(471);
	}
}
