.testimonials {
	// .testimonials__container

	&__container {
	}

	// .testimonials__content

	&__content {
		&:not(:last-child) {
			margin-bottom: toRem(26);
		}
		&.content {
			&_center {
			}

			.content__title {
			}
		}
	}

	// .testimonials__body

	&__body {
		position: relative;
	}

	// .testimonials__slider

	&__slider {
		padding: toRem(10);
	}

	// .testimonials__wrapper

	&__wrapper {
	}

	// .testimonials__slide

	&__slide {
	}
	&__navigation {
		@media (max-width: $tablet) {
			margin-top: toRem(30);
		}

		@media (max-width: $mobile) {
			margin-top: toRem(15);
		}
	}
}

.slide-testimonial {
	padding: toRem(60) toRem(100) toRem(33) toRem(110);
	background-color: var(--light-white-color-400);
	box-shadow: 0 toRem(4) toRem(4) 0 rgba(0, 0, 0, 0.1);
	display: grid;
	align-items: center;
	gap: toRem(60);
	font-size: toRem(24);
	line-height: 1.3;
	@media (min-width: $mobile) {
		grid-template-columns: 25.2% 1fr;
	}
	@media (max-width: $tablet) {
		padding: toRem(30);
	}
	@media (max-width: $mobile) {
		gap: toRem(25);
		padding-bottom: toRem(60);
		// padding-bottom: toRem(30);
	}

	// .slide-testimonial__image

	&__image {
		border-radius: toRem(20);
		overflow: hidden;
		padding-bottom: percent(317, 257);
	}

	// .slide-testimonial__content

	&__content {
		display: flex;
		flex-direction: column;
		gap: toRem(63);
		@media (max-width: $mobile) {
			flex-direction: column-reverse;
			justify-content: center;
			align-items: center;
			gap: toRem(42);
		}
	}

	// .slide-testimonial__quote

	&__quote {
		letter-spacing: toRem(0.24);
		max-width: toRem(610);
		position: relative;
		@media (max-width: $tablet) {
			font-size: toRem(20);
		}
		@media (max-width: $mobileSmall) {
			font-size: toRem(16);
		}
		&::before,
		&::after {
			content: "";
			position: absolute;
			width: toRem(33);
			flex: 0 0 toRem(33);
			height: toRem(28);
			background: url("@img/icons/quote.svg") 0 0 no-repeat;
		}
		&::before {
			left: toRem(-20);
			top: toRem(-50);
			@media (max-width: $tablet) {
				top: toRem(-20);
				left: toRem(-40);
			}
			@media (max-width: $mobile) {
				top: toRem(-40);
				left: toRem(-10);
			}
		}
		&::after {
			bottom: toRem(-30);
			transform: rotate(180deg) translateX(-50%);
			@media (max-width: $tablet) {
				bottom: toRem(-20);
			}
			@media (max-width: $mobile) {
				right: 0;
			}
		}
	}

	// .slide-testimonial__profile

	&__profile {
	}
}

.profile {
	display: flex;
	align-items: center;
	gap: toRem(10);
	@media (max-width: $mobile) {
		flex-direction: column;
		// gap: toRem(25);
	}
	// .profile__name

	&__name {
		font-weight: 600;
	}

	// .profile__rating

	&__rating {
	}
}
