.footer {
	padding-top: toRem(45);
	padding-bottom: toRem(45);
	background-color: var(--primary-color);

	color: var(--white-color);
	// .footer__container

	&__container {
		display: grid;

		@media (min-width: $mobile) {
			grid-template-columns: 39.4% 1fr;
			@include adaptiveValue("gap", 70, 30, 0, $containerWidth, 767.98);
		}
	}

	// .footer__content

	&__content {
		@media (min-width: $mobile) {
			padding-top: toRem(70);
		}
		&.content {
			.content__title {
				@media (min-width: $mobile) {
					max-width: toRem(385);
				}
				&:not(:last-child) {
					margin-bottom: toRem(27);
					@media (max-width: $tablet) {
						margin-bottom: toRem(20);
					}
				}
			}

			.content__text {
				line-height: 1.5;
				&:not(:last-child) {
					margin-bottom: toRem(20);
				}
			}
			@media (min-width: $mobile) {
				form {
					display: none;
				}
			}
			@media (max-width: $mobile) {
				text-align: center;
				form {
					width: 100%;
					.button {
						width: 100%;
					}
				}
			}
		}
	}
}
