.navigation {
	display: flex;

	align-items: center;

	@media (min-width: $tablet) {
		position: absolute;
		top: 50%;
		left: 0;
		transform: translateY(-50%);
		height: 100%;
		width: 100%;
		pointer-events: none;
		justify-content: space-between;
	}
	@media (max-width: $tablet) {
		justify-content: center;
		gap: toRem(30);
	}
	// .navigation__btn

	&__btn {
		flex: 00 toRem(81);
		width: toRem(81);
		height: toRem(81);
		border-radius: 50%;
		background-color: var(--light-white-color);
		pointer-events: all;
		transition: background-color 0.3s;
		display: flex;
		justify-content: center;
		align-items: center;
		&.swiper-button-disabled {
			opacity: 0.5;
			pointer-events: none;
		}
		img {
			width: toRem(30);
			height: toRem(30);
			@media (max-width: $mobile) {
				width: toRem(17);
				height: toRem(17);
			}
		}
		@media (max-width: $mobile) {
			flex: 0 0 toRem(45);
			width: toRem(45);
			height: toRem(45);
		}
		// .navigation__btn_prev

		&_prev {
			rotate: 180deg;
			@media (min-width: toEm(1330)) {
				transform: translateX(35%);
			}
		}

		// .navigation__btn_next

		&_next {
			@media (min-width: toEm(1330)) {
				transform: translateX(35%);
			}
		}
		@media (any-hover: hover) {
			&:hover {
				background-color: var(--hover-orange-color);
			}
		}
	}
}
