.about-us {
	// .about-us__container

	&__container {
	}

	// .about-us__content

	&__content {
		max-width: toRem(813);
		margin: 0 auto;

		&.content {
			// .content_center

			&_center {
			}

			.content__title {
				@media (max-width: $mobileSmall) {
					&:not(:last-child) {
						margin-bottom: toRem(30);
					}
				}
			}

			// .content__text

			.content__text {
				@media (max-width: $mobileSmall) {
					line-height: 1.5;
				}
			}
			.icon-text {
				display: none;
				display: inline-block;
				// .icon-text_fear

				&_fear {
					width: toRem(34);
					height: toRem(34);
					@media (max-width: $mobileSmall) {
						width: toRem(18);
						height: toRem(18);
					}
				}

				// .icon-text_brain

				&_brain {
					width: toRem(35);
					height: toRem(32);
					@media (max-width: $mobileSmall) {
						width: toRem(17);
						height: toRem(17);
					}
				}

				// .icon-text_schizophrenia

				&_schizophrenia {
					width: toRem(32);
					height: toRem(32);
					@media (max-width: $mobileSmall) {
						width: toRem(18);
						height: toRem(18);
					}
				}

				// .icon-text_alone

				&_alone {
					width: toRem(30);
					height: toRem(30);
					@media (max-width: $mobileSmall) {
						width: toRem(20);
						height: toRem(20);
					}
				}

				// .icon-text_help

				&_help {
					width: toRem(22);
					height: toRem(22);
					@media (max-width: $mobileSmall) {
						width: toRem(19);
						height: toRem(19);
					}
				}
				img {
					max-width: 100%;
				}
			}
		}
	}
}
