.hero {
	background-color: #cae4f1;
	position: relative;
	overflow: hidden;

	// .hero__container

	&__container {
		align-items: center;
		display: grid;
		@media (min-width: $mobileSmall) {
			min-height: 100vh;
		}
		@media (max-width: $mobileSmall) {
			min-height: toRem(425);
		}
	}

	// .hero__content

	&__content {
		padding-top: toRem(140);
		padding-bottom: toRem(30);
		position: relative;
		z-index: 2;
		@include adaptiveValue("max-width", 777, 240);
		&.content {
			.content__title {
				&:not(:last-child) {
					margin-bottom: toRem(32);
					@media (max-width: $mobile) {
						margin-bottom: toRem(8);
					}
				}
			}
			.content__text {
				@include adaptiveValue("font-size", 30, 16);
				&:not(:last-child) {
					margin-bottom: toRem(53);
					@media (max-width: $mobile) {
						margin-bottom: toRem(32);
					}
				}
			}
			.content__button {
			}
			@media (max-width: $mobile) {
				padding-top: toRem(100);
			}
			@media (max-width: $mobileSmall) {
				padding-top: toRem(120);
				padding-bottom: toRem(86);
			}
		}
	}

	&__image {
		position: absolute;
		right: 0;
		transform: translateY(17%);
		width: 55vw;
		height: 80%;
		@media (max-width: $mobile) {
			width: 70vw;
		}
		@media (max-width: $mobileSmall) {
			width: 100vw;
		}
	}

	&__image-path {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		mask-size: 100% 100%;
		mask-image: url("@img/mask.png");
		object-fit: cover;
		img {
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			object-fit: cover;
			@media (max-width: $mobile) {
				object-position: 72%;
			}
			// max-width: 100%;
		}
	}
}
